.animation {
  overflow: hidden;
  position: relative;
  display: block;
  height: 100vh;
  width: 100vw;
  background-color: #171e27;
}
.cube {
  width: 100vw;
  height: 100vh;
  background-color: white;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: Move 2s linear;
          animation: Move 2s linear;
  -webkit-animation-direction: forwards;
          animation-direction: forwards;
  margin: 0;
}

@-webkit-keyframes Move {
  0% {
    border-radius: 10px;
    width: 100px;
    height: 100px;
    top: calc(50vh - 100px/2);
    left: calc(50vw - 100px/2);
    -webkit-transform: translateX(50vw);
            transform: translateX(50vw);
  }
  20% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  27% {
    -webkit-transform: rotate(-20deg);
            transform: rotate(-20deg);
  }
  34% {
    -webkit-transform: rotate(-23deg);
            transform: rotate(-23deg);
  }
  40% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  70% {
    -webkit-transform: scale(1);
            transform: scale(1);
    border-radius: 10px;
  }
  80% {
    height: 100px;
    width: 100px;
    -webkit-transform: scale(0.4);
            transform: scale(0.4);
    border-radius: 0;
    top: calc(50vh - 100px/2);
    left: calc(50vw - 100px/2);
  }
  90% {
    -webkit-transform: scale(1);
            transform: scale(1);
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
  }
}

@keyframes Move {
  0% {
    border-radius: 10px;
    width: 100px;
    height: 100px;
    top: calc(50vh - 100px/2);
    left: calc(50vw - 100px/2);
    -webkit-transform: translateX(50vw);
            transform: translateX(50vw);
  }
  20% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  27% {
    -webkit-transform: rotate(-20deg);
            transform: rotate(-20deg);
  }
  34% {
    -webkit-transform: rotate(-23deg);
            transform: rotate(-23deg);
  }
  40% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  70% {
    -webkit-transform: scale(1);
            transform: scale(1);
    border-radius: 10px;
  }
  80% {
    height: 100px;
    width: 100px;
    -webkit-transform: scale(0.4);
            transform: scale(0.4);
    border-radius: 0;
    top: calc(50vh - 100px/2);
    left: calc(50vw - 100px/2);
  }
  90% {
    -webkit-transform: scale(1);
            transform: scale(1);
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
  }
}

