}

.job:hover {
    background-color:#FFB605;
}

.joblist {
  margin-bottom: 20px; 
}
.navbar {
  background-color: white;
  width: 100%;
  margin: 0;
  border: none;
}

.navbar-brand {
  position: absolute;
  top: -1.1em;
  border: none;
}

.navbar-brand>img {
  height: 280%;
  width: auto;
}
.narrator-pic {
  height: 70%;
  width: 70%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px; 
  max-height: 400px;
  max-width: 400px;
}

.story-title {
  text-align: center; 
}

.story {
  margin-top: 80px; 
}

.story-button {
  margin-top: 0; 
}


